<template>
  <div>
    <el-form size="mini" label-width="80px">
      <el-form-item label="产品名称">
        <el-input v-model="form.name" style="width: 200px" />
      </el-form-item>
      <el-form-item label="供应商">
        <el-input v-model="form.shopName" style="width: 200px" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="apiGetGoodsList">查询</el-button>
      </el-form-item>
    </el-form>

    <el-table ref="multipleTable" :data="addGoodList" style="width: 100%" size="mini" height="430" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column prop="categoryId" label="商品编号" width="200" align="center" show-overflow-tooltip />
      <el-table-column prop="name" label="商品名称" width="200" align="center" />
      <el-table-column prop="categoryName" label="分类" align="center" show-overflow-tooltip />
      <el-table-column prop="shopName" label="供应商" width="200" align="center" show-overflow-tooltip />
      <el-table-column prop="originalPrice" label="销售价（元）" align="center" />
      <el-table-column prop="totalInventory" label="库存" align="center" />
      <el-table-column fixed="right" label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="onAdd(scope)">添加</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination background layout="prev, pager, next" :total="goodsTotal" @current-change="handleCurrentChange" />
    <el-divider />
  </div>
</template>

<script>
export default {
  name: 'AddGoodsCmp',

  data() {
    return {
      childAddGoodList: [], // 选择的商品
      addGoodList: [], // 商品列表
      goodsTotal: 0, // 添加商品分页总条数
      pageNo: 1, // 分页当前页
      multipleSelection: [], // 选中的数据
      form: {}, // 添加商品时的模糊查询
    }
  },

  mounted() {

  },

  created() {
    this.apiGetGoodsList()
  },

  methods: {
    // 点击表中的添加
    onAdd(scope) {
      this.$refs.multipleTable.toggleRowSelection(this.addGoodList[scope.$index])
    },
    // 分页
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo
      this.apiGetGoodsList()
    },
    // 选中
    handleSelectionChange(val) {
      console.log(val)
      this.multipleSelection = val
    },

    // api --------------------------------------
    // 查询商品列表
    apiGetGoodsList() {
      const DATA = {
        pageNo: this.pageNo,
        state: 1,
        ...this.form
      }
      this.$http.post('/woho/product/head/query', DATA)
        .then((res) => {
          this.addGoodList = res.data.data.list
          this.goodsTotal = res.data.data.total
        })
    }
    // api --------------------------------------
  }
}
</script>

<style lang="scss" scoped>

</style>
