<template>
  <div>
    <LayoutBox one="立即报名">
      <h4>关联商品</h4>
      <el-table :data="goodsAndActivity" style="width: 100%" stripe>
        <!-- <el-table-column prop="code" label="商品编号" align="center" /> -->
        <el-table-column prop="name" label="商品名称" align="center" />
        <el-table-column label="限购量" align="center">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model.number="scope.row.restrictionQuantity" type="number" />
            <p v-else>{{ scope.row.restrictionQuantity }}</p>
          </template>
        </el-table-column>
        <el-table-column label="运费" align="center">
          <template slot-scope="scope">
            <el-input v-if="updateIndex === scope.$index" v-model.number="scope.row.postFee" />
            <p v-else>{{ scope.row.postFee }}</p>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button v-if="updateIndex === scope.$index" style="color: #ff8711; margin: auto 5px" type="text" @click="onSave(scope)">完成</el-button>
            <el-button v-else class="btn_update" type="text" @click="onUpdate(scope)">编辑</el-button>
            <el-button class="btn_delete" type="text" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="btn_box">
        <el-button class="btn_add" type="text" icon="el-icon-plus" @click="dialogVisible = true">添加商品</el-button>
      </div>
      <el-divider />
      <div class="btn_group">
        <el-button type="danger" size="small" style="width: 100px" round @click="onApply">完成报名</el-button>
        <router-link to="/mark/plat/activity"><el-button size="small" style="width: 100px" round>取消</el-button></router-link>
      </div>
    </LayoutBox>
    <el-dialog :visible.sync="dialogVisible" title="添加商品" fullscreen>
      <AddGoodsCmp ref="AddGoodsCmp" />
      <span slot="footer" class="dialog-footer">
        <el-button round @click="dialogVisible = false">返 回</el-button>
        <el-button type="danger" round @click="addAll()">添 加</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import AddGoodsCmp from '@/views/marketing/mark/add_goods_cmp/index'

export default {
  name: 'Goods',

  components: {
    LayoutBox, AddGoodsCmp
  },

  data() {
    return {
      dialogVisible: false, // 打开或者关闭添加商品的列表
      goodsAndActivity: [], // 关联的商品列表
      updateIndex: '', // 编辑哪一行
      activityData: {} // 活动信息
    }
  },

  created() {
    this.activityData = this.$route.query
    this.activityData.activeId = this.$route.query.id
  },

  methods: {
    // 分页
    handleCurrentChange() {
    },
    // 点击表内的删除
    onDelete(scope) {
      this.goodsAndActivity.splice(scope.$index, 1)
    },
    // 点击表内的编辑
    onUpdate(scope) {
      this.updateIndex = scope.$index
    },
    // 点击表内的完成
    onSave(scope) {
      console.log(scope.row)
      this.updateIndex = ''
    },
    // 点击完成报名
    onApply() {
      if (this.goodsAndActivity.length < 1) {
        return this.$message.error('至少选择一件商品')
      }
      this.goodsAndActivity.map((item) => {
        if (item.restrictionQuantity > 0) {
          1 + 1
        } else {
          this.$message.error('限购量不能为空')
          throw SyntaxError('强制报错停止map')
        }
        if (item.postFee === undefined) {
          item.postFee = 0
        }
        Object.assign(item, this.activityData)
      })
      this.apiAddActivityProductRel()
    },
    // 点击添加（批量）
    addAll() {
      let successCut = 0 // 成功的数量
      let errCut = 0 // 失败的数量
      for (let i = 0; i < this.$refs.AddGoodsCmp.$data.multipleSelection.length; i++) {
        if (this.goodsAndActivity.includes(this.$refs.AddGoodsCmp.$data.multipleSelection[i])) {
          errCut++
        } else {
          successCut++
          this.$refs.AddGoodsCmp.$data.multipleSelection[i].prodId = this.$refs.AddGoodsCmp.$data.multipleSelection[i].id
          this.goodsAndActivity.push(this.$refs.AddGoodsCmp.$data.multipleSelection[i])
        }
      }
      if (successCut !== 0) {
        this.dialogVisible = false
        this.$message.success(`有${successCut}个添加成功`)
      }
      if (errCut !== 0) {
        this.$notify.error({
          title: '错误',
          message: `有${errCut}个重复了`
        })
      }
    },

    // api --------------------------------------
    // 新增活动商品关系
    apiAddActivityProductRel() {
      this.$http.post('/woho/activity/actProdRel/insert', this.goodsAndActivity)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('报名成功')
            this.$router.push('/mark/plat/activity/myActivity')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
    // api --------------------------------------
  }
}
</script>

<style lang="scss" scoped>
  .btn_box{
    text-align: center;
    width: 100%;
    .btn_add{
      color: #5bd9be;
      font-size: 16px;
    }
  }
  .btn_group{
    width: 100%;
    text-align: center;
  }
  .btn_update{
    margin: auto 5px;
    color: #5bd9be;
  }
  .btn_delete{
    margin: auto 5px;
    color: #ff2232;
  }
</style>
